/**
 * Bars Components
 * --------------------------------------------------
 */
.c-bar-main-title {
  margin: 0 auto 30px;
  height: 10px;
  width: 70%;
  background-color: #52edc7; }

.c-bar-section-title {
  margin: 0 auto 80px;
  height: 10px;
  width: 100px;
  background-color: #999; }

/**
 * Buttons Components
 * --------------------------------------------------
 */
.c-btn {
  border-radius: 4px;
  height: 48px;
  width: 150px;
  display: block;
  font-size: 20px;
  font-weight: bold;
  transition: background-color .15s ease-in-out; }

.c-btn-icon {
  background-repeat: no-repeat;
  text-indent: 50px;
  line-height: 48px; }

.c-btn-linkedin {
  color: #fff;
  background-color: #007bb6; }
  .c-btn-linkedin:hover {
    background-color: #006a9d; }

.c-btn-icon-linkedin {
  background-image: url("../images/figures-svg.svg#logo-linkedin");
  background-position-y: -3px; }

.c-btn-github {
  color: #000;
  background-color: #ccc; }
  .c-btn-github:hover {
    background-color: #bfbfbf; }

.c-btn-icon-github {
  background-image: url("../images/figures-svg.svg#logo-git-hub--black"); }

/**
 * Contacts Module
 * --------------------------------------------------
 */
.m-contacts_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.m-contact {
  margin: 10px 0;
  color: #000;
  font-size: 22px;
  line-height: 1; }

.m-contact--email {
  text-decoration: underline; }

.m-contact-whatsapp {
  cursor: text; }

.m-contact_icon {
  padding-left: 30px;
  background: 0 0 no-repeat; }

.m-contact_icon-address {
  background-image: url("../images/figures-svg.svg#icon-map-marker-alt"); }

.m-contact_icon-email {
  background-image: url("../images/figures-svg.svg#icon-at"); }

.m-contact_icon-whatsapp {
  background-image: url("../images/figures-svg.svg#logo-whatsapp"); }

/**
 * Freelancer Module
 * --------------------------------------------------
 */
.m-freelancer-photo {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  border: 4px solid #fff;
  margin-bottom: 50px;
  box-shadow: inset 0 1.5px 3px 0 rgba(0, 0, 0, 0.15), 0 1.5px 3px 0 rgba(0, 0, 0, 0.15); }

.m-freelancer-info {
  color: white;
  text-align: center; }
  .m-freelancer-info_name {
    margin-bottom: 30px;
    font-family: Montserrat, sans-serif;
    font-size: 36px;
    font-weight: bold; }
    @media (min-width: 600px) {
      .m-freelancer-info_name {
        font-size: 48px; } }
  .m-freelancer-info_description {
    margin-bottom: 50px;
    max-width: 540px;
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    line-height: 36px; }
    @media (min-width: 600px) {
      .m-freelancer-info_description {
        font-size: 22px; } }

.m-freelancer-buttons {
  display: flex; }
  .m-freelancer-buttons .c-btn:first-child {
    margin-right: 20px; }

/**
 * Offers Module
 * --------------------------------------------------
 */
.m-offers {
  background-color: #fff; }

.m-offers_content {
  padding: 0 13px; }
  .m-offers_content:before, .m-offers_content:after {
    content: "";
    display: table; }
  .m-offers_content:after {
    clear: both; }

.m-offer {
  width: 100%;
  float: left; }
  @media (min-width: 600px) {
    .m-offer {
      width: 50%; } }
  @media (min-width: 900px) {
    .m-offer {
      width: 33.33%; } }
  @media (min-width: 1200px) {
    .m-offer {
      width: 25%; } }

.m-offer_title {
  height: 100px;
  padding-bottom: 25px;
  display: flex;
  align-items: flex-end;
  justify-content: center; }

.m-offer_informations {
  padding-bottom: 70px;
  text-align: center; }

.m-offer_icon-wrap {
  color: #333;
  font-size: 40px;
  text-align: center; }

.m-offer_icon {
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: 50px 50px; }

.m-offer_icon-attendance {
  background-image: url("../images/figures-svg.svg#icon-users"); }

.m-offer_icon-comments {
  background-image: url("../images/figures-svg.svg#icon-comments"); }

.m-offer_icon-delivery {
  background-image: url("../images/figures-svg.svg#icon-tasks"); }

.m-offer_icon-exclusivity {
  background-image: url("../images/figures-svg.svg#icon-crosshairs"); }

.m-offer_icon-experience {
  background-image: url("../images/figures-svg.svg#icon-star"); }

.m-offer_icon-invoice {
  background-image: url("../images/figures-svg.svg#icon-invoice"); }

.m-offer_icon-termination {
  background-image: url("../images/figures-svg.svg#icon-hand-paper"); }

.m-offer_icon-warranty {
  background-image: url("../images/figures-svg.svg#icon-bug"); }

/**
 * Projects Module
 * --------------------------------------------------
 */
.m-projects {
  background-color: #f8f9fa; }

.m-projects_wrapper .m-project:nth-child(1) {
  background-color: #568dac; }
  .m-projects_wrapper .m-project:nth-child(1) .m-project_image-inner {
    background-color: #78a4bd; }

.m-projects_wrapper .m-project:nth-child(2) {
  background-color: #e2ac4c; }
  .m-projects_wrapper .m-project:nth-child(2) .m-project_image-inner {
    background-color: #e9c078; }

.m-projects_wrapper .m-project:nth-child(3) {
  background-color: #f77871; }
  .m-projects_wrapper .m-project:nth-child(3) .m-project_image-inner {
    background-color: #faa6a1; }

.m-projects_wrapper .m-project:nth-child(4) {
  background-color: #a284c2; }
  .m-projects_wrapper .m-project:nth-child(4) .m-project_image-inner {
    background-color: #bca6d3; }

.m-projects_wrapper .m-project:nth-child(5) {
  background-color: #404447; }
  .m-projects_wrapper .m-project:nth-child(5) .m-project_image-inner {
    background-color: #585e62; }

.m-project {
  margin-bottom: 20px;
  padding: 70px 13px;
  color: white; }

.m-project-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.m-project_image {
  height: 200px;
  width: 200px;
  position: relative;
