  @media (max-width: 599px) {
    .m-project_image {
      display: none; } }

.m-project_laptop--black-image {
  background: transparent url("../images/figures-svg.svg#icon-laptop--black") no-repeat; }

.m-project_laptop--white-image {
  background: transparent url("../images/figures-svg.svg#icon-laptop--white") no-repeat; }

.m-project_mobile-image {
  background: transparent url("../images/figures-svg.svg#icon-mobile") no-repeat; }

.m-project_laptop-image-inner {
  height: 77px;
