    .m-project_info {
      width: 55%; } }
  @media (min-width: 900px) {
    .m-project_info {
      width: 65%; } }

.m-project_info-top {
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between; }

.m-project_info-top_title:hover {
  text-decoration: underline; }

.m-project_info-top_title > h2 {
  font-size: 36px;
