    padding-left: 10px; }
    .m-project_info-tags > li:first-child {
      list-style: none;
      padding-left: 0; }
      .m-project_info-tags > li:first-child > span {
        position: static; }
    .m-project_info-tags > li > span {
      position: relative;
      left: -5px; }

.m-project_info-description {
